
import BScroll from 'better-scroll'
import Rater from 'vux/src/components/rater/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


export default {
name: 'goodsDetail',
directives: {
TransferDom
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
Rater,
Previewer,
TransferDom
},
data () {
return {
currentCommentId: '',
commentsValue: '',
showToast: false,
previewList: [],
pulldownMsg: '下拉刷新',
pullupMsg: '加载更多',
alertHook: 'none',
isMoreData: true,
commentsInfo: [],
params: {
goodId: '',
page: 1,
pageSize: 10
},
baseInfo: {},
status: 1,
data3: 4.5,
index: 0,
list2: ['详情', '评价'],
data: [],
list: [{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
w: 800,
h: 400
},
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
w: 1200,
h: 900
}, {
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwwcynw2j20p00b4js9.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwwcynw2j20p00b4js9.jpg'
}, {
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
w: 800,
h: 400
},
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
w: 1200,
h: 900
}],
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
methods: {
showDialog(id) {
this.currentCommentId = id
this.showToast = true
},
replyComment () {
if(!this.commentsValue){
this.$vux.toast.show({
text: '请输入内容',
position: 'middle',
type: 'warn'
})
return
}
this.$http.fetchPost('/merchant/good/add/commentreply',{commentId: this.currentCommentId,merchantId:this.$store.state.merchantId,replyText:this.commentsValue}).then((res)=>{
if(res.data.code == 200) {
this.$vux.toast.show({
text: '评论成功',
position: 'middle',
type: 'warn'
})
this.commentsInfo.comments.map((item)=>{
if(item.id === this.currentCommentId){
item.replyText = this.commentsValue
}
})
this.currentCommentId = ''
this.commentsValue = ''
this.showToast = false
}else{
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
getBack () {
this.$router.go(-1)
},
show (parent,index) {
this.previewList = this.commentsInfo.comments[parent].commentImageInfo
this.previewList.map((item)=>{
item.msrc = item.src = item.url
item.w = item.h = 0
})
this.$nextTick(()=>{
this.$refs.previewer.show(index)
})
},
getData () {
this.$http.fetchGet('/merchant/good/get/detail', {goodId: this.$route.query.id}).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.baseInfo = res.data.data
})
},
getList () {
this.$http.fetchGet('/merchant/good/get/comments', this.params).then((res) => {
this.commentsInfo = res.data.data
if (this.commentsInfo.comments.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}
this.initScroll()
})
},
refreshData () {
this.$refs.content.style.height = 'auto'
this.pullupMsg = '加载中。。。'
this.params.page = 1
this.$http.fetchGet('/merchant/good/get/comments', this.params).then((res) => {
this.commentsInfo = res.data.data
if (this.commentsInfo.comments.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}

this.pullupMsg = '加载更多'

this.scroll.refresh()
})
},
loadMoreData () {
if (this.commentsInfo.comments.length < this.params.pageSize || this.commentsInfo.comments.length < this.params.pageSize * this.params.page) {
this.isMoreData = false
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.params.page++
this.$http.fetchGet('/merchant/good/get/comments', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
if (this.commentsInfo.comments.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
}
if (res.data.data.comments.length > 0) {
res.data.data.comments.map((item) => {
this.commentsInfo.comments.push(item)
})
} else {
this.isMoreData = false
}

this.pulldownMsg = '下拉刷新'

this.scroll.refresh()
})
},
checkGoods (status) {
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchGet('/merchant/good/update/status', {goodId: this.$route.query.id, operate: status}).then((res) => {
this.$vux.loading.hide()
if (res.data.code === 200) {
this.$vux.toast.show({
text: '操作成功',
position: 'middle'
})
if(status == 1) {
this.$router.go(-1)
}
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
getEdit () {
this.$router.push({
name: 'goodsAdd',
query: {
id: this.$route.query.id
}
})
},
initScroll () {
this.$nextTick(() => {
this.scroll = new BScroll(this.$refs.wrapper, { // 初始化better-scroll
probeType: 1, // 1 滚动的时候会派发scroll事件，会截流。2滚动的时候实时派发scroll事件，不会截流。 3除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
click: true 
})

this.scroll.on('scroll', (pos) => {
if (pos.y > 30) {
this.pulldownMsg = '释放立即刷新'
}
})

this.scroll.on('touchEnd', (pos) => { // 上拉刷新
if (pos.y > 30) {
this.refreshData()
} else if (pos.y < (this.scroll.maxScrollY - 30)) { // 下拉加载
this.loadMoreData()
}
})
})
}
},
created () {
this.$vux.loading.show({
text: '加载中...'
})
this.params.goodId = this.$route.query.id
this.status = this.$route.query.tabIndex
this.getData()
this.getList()
}
}



